import React from 'react'
import Helmet from 'react-helmet'
import styled from 'styled-components'

import SEO from '../components/SEO'
import SiteHeader from '../components/Layout/Header'
import config from '../../data/SiteConfig'
import TableOfContents from '../components/Layout/TableOfContents'
import MetaBox from '../components/Layout/MetaBox'

export default class LessonTemplate extends React.Component {
  render() {
    const { slug } = this.props.pathContext
    const isSkill = this.props.pathContext.type
    const postNode = this.props.data.postBySlug
    const post = postNode.frontmatter
    if (!post.id) {
      post.id = slug
    }
    if (!post.id) {
      post.category_id = config.postDefaultCategoryID
    }
    return (
      <div>
        <Helmet>
          <title>{`${post.title} | ${config.siteTitle}`}</title>
        </Helmet>
        <SEO postPath={slug} postNode={postNode} postSEO />
        <BodyGrid>
          <HeaderContainer>
            <SiteHeader location={this.props.location} />
          </HeaderContainer>
          <BodyContainer>
            {/* <h1>{post.title}</h1> */}
            <div dangerouslySetInnerHTML={{ __html: postNode.html }} />
          </BodyContainer>
          <ToCContainer className={(isSkill) ? 'hidden-toc' : ''}>
            <TableOfContents
              chapters={(isSkill) ? this.props.data.tableOfContentsSkills.chapters : this.props.data.tableOfContents.chapters}
            />
            <MetaBox
              className={(!isSkill) ? 'hidden-toc' : ''}
              meta={{author:'Optic Team'}}
            />
          </ToCContainer>
        </BodyGrid>
      </div>
    )
  }
}

const BodyGrid = styled.div`
  height: 100vh;
  display: grid;
  grid-template-rows: 50px 1fr;
  grid-template-columns: 1fr 320px;

  @media screen and (max-width: 768px) {
    display: flex;
    flex-direction: column;
    height: inherit;
  }
`

const BodyContainer = styled.div`
  grid-column: 1 / 2;
  grid-row: 2 / 3;
  justify-self: center;
  max-width: 700px;
  padding: ${props => props.theme.sitePadding};
  &.hidden-toc {
    grid-column: 1 / 3;
  }
  @media screen and (max-width: 768px) {
    order: 3;

    &.hidden-toc {
      margin-top: 50px;
    }
  }

  & > div {
    max-width: ${props => props.theme.contentWidthLaptop};
    margin: auto;
    
    & > h1 {
      color: ${props => props.theme.accentDark};
      @media screen and (max-width: 768px) {
        margin-top: 0;
      }
    }
  }

  @media screen and (max-width: 600px) {
    max-width: 100%
  }
`

const HeaderContainer = styled.div`
  grid-column: 1 / 3;
  grid-row: 1 / 2;
  z-index: 2;
  position: fixed;
  width: 100%;
  
  @media screen and (max-width: 768px) {
    order: 1;
  }
`

const ToCContainer = styled.div`
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  background: none;
  overflow-y: auto;
  position: fixed;
  right: 0;
  width: 320px;
  height: calc(100vh - 50px);
  top: 50px;
  
  &.hidden-toc > div:first-child{
    display: none;
  }
  @media screen and (max-width: 768px) {
    order: 2;
    top: 0;
    margin-top: 50px;
    position: relative;
    height: auto;
    width: 100%;
    overflow: inherit;
  }
`

/* eslint no-undef: "off" */
export const pageQuery = graphql`
  query LessonBySlug($slug: String!) {
    postBySlug: markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      timeToRead
      excerpt
      frontmatter {
        title
        cover
        date
        category
        tags
      }
    }
    tableOfContents: lessonsJson {
      chapters {
        title
        entries {
          entry {
            id
            childMarkdownRemark {
              fields {
                slug
              }
              frontmatter {
                title
                type
              }
            }
          }
        }
      }
    }
    tableOfContentsSkills: skillsJson {
      chapters {
        title
        entries {
          entry {
            id
            childMarkdownRemark {
              headings {
                value
                depth
              }
              fields {
                slug
              }
              frontmatter {
                title
                type
              }
            }
          }
        }
      }
    }
  }
`



