import React from 'react'
import styled from 'styled-components'
import Link from 'gatsby-link'

class MetaBox extends React.Component {

    render() {
      const { meta } = this.props
      return (
        <MetaWrapper>
          <div className="toc-main">
            {Object.keys(meta).map((key, index) => (
              <div key={index}>
                <MetaTitle>{key.toUpperCase()}: {meta[key]}</MetaTitle>
              </div>
            ))}
          </div>
        </MetaWrapper>
      )
  }
}

const MetaWrapper = styled.div`
  padding: ${props => props.theme.sitePadding};
  margin: 0;
`
const MetaTitle = styled.h5`
  font-weight: 200;
  color: ${props => props.theme.brand};
  border-left: 3px solid ${props => props.theme.lightBlue};
  padding-left: 1em;
`

export default MetaBox


