import React from 'react'
import Link from 'gatsby-link'
import styled from 'styled-components'
import { FaChevronDown } from 'react-icons/lib/fa'

const Links = ({ entries }) => (
  <StyledLinkList>
    {entries.map(({ entry }, key) => (
      <EntryListItem key={key}>
        <Link to={entry.childMarkdownRemark.fields.slug} activeClassName="active-toc-link">
          <EntryTitle>{entry.childMarkdownRemark.frontmatter.title}</EntryTitle>
        </Link>
      </EntryListItem>
    ))}
  </StyledLinkList>
)

const ChapterList = ({ chapters, entries, title, level = 0, toggleOpen, isOpen }) => (
  <StyledChapterList>
    {title && (
      <ChapterListItem
        key={`${title}${level}`}
        className={'head'}
        id={`head-${title}`}
        onClick={() => toggleOpen()}
      >
        <ChapterTitle level={level}>{title}</ChapterTitle>
        <FaChevronDown className={(isOpen) ?'chevron chevron-open': 'chevron'} id={`chevron-${title}`} />
      </ChapterListItem>
    )}
    <ChapterListItem id={`list-${title}`} style={(isOpen) ? {} : {'display': 'none'}}>
      {entries && <Links entries={entries} />}
    </ChapterListItem>
    <ChapterListItem id={title}>
      {chapters &&
        chapters.map((chapter, index) => (
          <ChapterList {...chapter} level={level + 1} key={`${index}`} />
        ))}
    </ChapterListItem>
  </StyledChapterList>
)

export default class TableOfContents extends React.Component {

  constructor(props) {
    super(props)

    if (typeof window !== `undefined`) {
      this.state = {
        chaptersOpen: props.chapters.map(() => (window.innerWidth > 768))
      }
    } else {
      this.state = {
        chaptersOpen: props.chapters.map(() => true)
      }
    }

    this.toggleOpen = this.toggleOpen.bind(this)
  }

  toggleOpen(updatedIndex) {
    this.setState({chaptersOpen: this.state.chaptersOpen.map((bool, index) => (updatedIndex === index) ? !bool : bool)})
  }

	render() {
    const { chapters } = this.props
    return (
      <TOCWrapper>
        <div className="toc-main">
          {chapters.map((chapter, index) => (
            <ChapterList
              {...chapter}
              key={index}
              toggleOpen={()=> this.toggleOpen(index)}
              isOpen={this.state.chaptersOpen[index]}
            />
          ))}
        </div>
      </TOCWrapper>
    )
	}
}

const TOCWrapper = styled.div`
  padding: ${props => props.theme.sitePadding};
  margin: 0;
`

const StyledChapterList = styled.ol`
  list-style: none;
  margin: 0;
  .head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
  }
`

const StyledLinkList = styled.ol`
  list-style: none;
`

const EntryTitle = styled.h6`
  display: inline-block;
  font-weight: 200;
  color: ${props => props.theme.accentDark};
  margin: 0;
  line-height: 1.5;
  border-bottom: 1px solid transparent;
  text-decoration: none;
`

const ChapterListItem = styled.li`
  margin: 0;

  .chevron {
    height: 15px;
    width: 15px;
    color: ${props => props.theme.accentDark};
    transform: rotate(0deg);
    transition: all 300ms ease-in-out;
  }
  
  .chevron-open {
    transform: rotate(180deg);
  }
  
`

const EntryListItem = styled.li`
  margin: 0;
  a:hover, .active-toc-link {
    border-bottom: 1px solid ${props => props.theme.accentDark};
  }
`

const ChapterTitle = styled.h5`
  font-weight: ${({ level }) => {
    switch (level % 3) {
      case 1:
        return '600'
      case 2:
        return '400'
      default:
        return '200'
    }
  }};
  font-size: ${({ level }) => {
    switch (level % 3) {
      case 1:
        return '2.0rem'
      case 2:
        return '1.9rem'
      default:
        return '2.1rem'
    }
  }};
  color: ${({ level, theme }) => {
    switch (level % 3) {
      case 1:
        return `${props => props.theme.accentDark}`
      case 2:
        return theme.accent
      default:
        return theme.brand
	}
  }};
  border-left: 3px solid ${props => props.theme.lightBlue};
  padding-left: 1em;
`



